import React, {Component} from "react";
import {connect} from "react-redux";
import {
    setSelectedModel,
    setSelectedProducer,
    fetchComparatorConfig,
    fetchProducers,
    clearModelsForBrand
} from "eshop/modules/simfree/actions/comparator";
import {
    getSelectedComparatorProducer,
    getProducers,
    getSelectedComparatorModel,
    getComparatorDevices,
    getModelsForBrand
} from "eshop/modules/simfree/selectors";
import {OraCmpSelect} from "../../../../components/OraCommonComponents";
import ComparatorAddDeviceModal from "./ComparatorAddDeviceModal";
import ActionType from "eshop/modules/simfree/fetchComparatorConfigActionType";

class ComparatorEmptyDeviceTab extends Component {

    constructor(props) {
        super(props);
    }

    componentDidMount(){
        if (document.getElementById("comparator-section0") != null) {
            OPL.UI.initModules(document.getElementById("comparator-section0"));
        }
    }

    getProducersNames() {
        return this.props.producers
            .map(producer => {
                return {value: producer, description: producer}
            });
    }

    handleProducerChange({value}) {
        this.props.setSelectedModel("");
        this.props.setSelectedProducer(value);
    }

    getModelsForBrand() {
        return this.props.modelsForBrand.map(model => {
            return {value: model.productId, description: model.name}
        })
    }

    updateSelectedModel({value}) {
        this.props.updateComparatorConfig(value, this.props.comparatorUid);
        this.props.setSelectedProducer("");
        this.props.setSelectedModel("");
        this.props.clearModelsForBrand();
        this.props.fetchProducers();
    }


    render() {
        return (
            <section id={"comparator-section"+this.props.index} className="opl-compare-products__section--box">
                <div className="opl-compare-products__section--select-box">
                    <div className="opl-compare-products__section--select-box-desktop">
                        <span className="g-icon g-icon--device-smartphone g-icon--xxl u-padding-top-m u-padding-m"/>
                        <div className="o-floating-label o-select u-spacing-l"
                             data-js-module="core/modules/floating-label" style={{width: "100%"}}>
                            <OraCmpSelect id={"selectBrand" + this.props.index}
                                       className="u-small-spacing-l u-padding"
                                       disabled={this.props.index != 0}
                                       emptyOption={{value: '', description: 'Wybierz marke'}}
                                       withEmptyOption={true}
                                       selected={this.props.index == 0 ? this.props.selectedProducer : ''}
                                       ariaLabel="Wybierz marke"
                                       onChange={this.handleProducerChange.bind(this)}
                                       options={this.getProducersNames()}/>
                            <OraCmpSelect id={"selectModel" + this.props.index}
                                       className="u-small-spacing-l u-medium-spacing-l"
                                       disabled={this.props.index != 0 || this.props.modelsForBrand.length == 0}
                                       emptyOption={{value: '', description: 'Wybierz model'}}
                                       withEmptyOption={true}
                                       selected={this.props.index == 0 ? this.props.selectedModel : ''}
                                       ariaLabel="Wybierz model"
                                       onChange={this.updateSelectedModel.bind(this)}
                                       options={this.getModelsForBrand()}/>
                        </div>
                    </div>
                    <div className="opl-compare-products__section--select-box-mobile">
                        <ComparatorAddDeviceModal title="Porównaj" trigger={"opl-comparator__modal-trigger"} comparatorComponentUid={this.props.comparatorUid}/>
                        <a id="opl-comparator__modal-trigger" className="opl-btn--box o-btn o-modal__trigger" href="#opl-compare-products__modal"
                           data-js-module="core/modules/modal"><span
                            className="g-icon--xs g-icon g-icon--plus"/><span/>Dodaj<br/>urządzenie</a>
                    </div>
                </div>
            </section>

        )
    }

}

const mapStateToProps = state => ({
    selectedProducer: getSelectedComparatorProducer(state),
    selectedModel: getSelectedComparatorModel(state),
    selectedDevices: getComparatorDevices(state),
    producers: getProducers(state),
    modelsForBrand: getModelsForBrand(state)
});

const mapDispatchToProps = (dispatch) => ({
    setSelectedProducer: (producer) => dispatch(setSelectedProducer(producer)),
    setSelectedModel: (model) => dispatch(setSelectedModel(model)),
    updateComparatorConfig: (devices, pk) => dispatch(fetchComparatorConfig(devices, pk, ActionType.ADD_DEVICE)),
    clearModelsForBrand: () => dispatch(clearModelsForBrand()),
    fetchProducers: () => dispatch(fetchProducers())
});

export default connect(mapStateToProps, mapDispatchToProps)(ComparatorEmptyDeviceTab);